

.wrapper{
    display: flex;
    align-items: center;
    padding: 20px;
    border-bottom: 1px dashed var(--yellow-theme-main-colour);


    h2{
        font-size: 36px;
        font-weight: 700;
        letter-spacing: 1.5px;
        line-height: 1rem;
        text-transform: capitalize;
        color: var(--yellow-theme-main-colour);
        margin-right: auto;
    }

    span{
        svg{
            color: var(--yellow-theme-main-colour)
        }
    }
}